@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=wrap");

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body, #root {
  height: 100vh;
  margin: 0;
}

body, input, select, textarea, button {
  font-size: 1.6rem;
  font-family: Montserrat, Helvetica, Arial, sans-serif;
}



header {
  background-color: rgb(4, 56, 228);
  display: flex;
  align-items: center;
  padding: 0.5rem;
}

header a {
  color: #fff;
  outline: none;
  text-decoration: none;
}

a:hover {
  color: rgb(255, 255, 10)
}


footer {
  background-color: rgb(4, 56, 228);
  display: flex;
  justify-content: center;
  align-items: center;
}

.content {
  display: flex;
  flex-wrap: wrap;
}

.main {
  flex: 3 60rem;
}

.sidebar {
  flex: 1 20rem;
}

a {
  text-decoration: none;
}

